import { ChartColorPalette, palettes } from '../types/chart-color';

export function getChartColors(
    palette: ChartColorPalette | string[],
): string[] | null {
    if (Array.isArray(palette)) {
        return palette;
    }
    if (palette && palettes[palette]) {
        return palettes[palette];
    }
    return null;
}

export function generateColourPalette(dataCount: number): string[] {
    const startHue = 0;
    const hueStep = 360 / dataCount;
    const saturation = 75;
    const lightness = 75;
    const colours: string[] = [];
    for (let i = 0; i < dataCount; i++) {
        const variantHue = startHue + ((hueStep * i) % 360);
        colours.push(`hsl(${variantHue}, ${saturation}%, ${lightness}%)`);
    }
    return colours;
}
